/* search View style */
.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 55;
  background-color: #fafafa;
  margin-top: 3%;
  /* padding-left: 2% ; */
}

/* Search bar styling */
.search-bar {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 2% 2%;
  background-color: white;
  width: 90%;
}

/* Search input styling */
.search-input {
  border: none;
  outline: none;
  flex-grow: 1;
  margin: 0 10px;
  font-size: 16px;
}

/* Icon styling */
.icon {
  font-size: 15px;
  cursor: pointer;
}

/* Adjust for better UI */
.search-icon {
  color: #555;
}

.mic-icon {
  color: #007bff;
}

/* Filter View styles  */
.filter-container {
  display: flex;
  padding-left: 2%;
  align-items: center;
  gap: 10px; /* Space between buttons */
  margin-top: 15px;
  width: 100%;
  overflow-x: auto; /* Enable horizontal scrolling */
  margin-bottom: 15px;
  scrollbar-width: none;
  padding-left: 3%;
}

/* Default button styling */
.filter-button {
  padding: 6px 0;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  width: 50%;
  flex: 0 0 27%;
}

/* Inactive button styling */
.filter-button.inactive {
  background-color: #ccc;
  color: #666;
}

/* Active button styling */
.filter-button.active {
  background-color: #28a745;
  color: white;
}

/* Button hover effect */
.filter-button:hover {
  opacity: 0.8;
}

/* MENU ITEM STYLE */
.menu-card {
  width: 180px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin: 10px;
  background: #fff;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  flex: 0 0 25%;
}

.menu-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.menu-image {
  width: 100%;
  height: 80px;
  object-fit: cover;
}

.menu-info {
  padding: 0;
  height: 80px; /* Adjust to fit the button */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.menu-title {
  font-size: 12px;
  font-weight: bold;
  margin: 0;
}

.menu-price {
  font-size: 12px;
  color: #004422;
  margin: 0;
}

/* Button styling */
.add-button {
  background-color: #009944;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-button:hover {
  background-color: #004422;
}

/* view cart box styles------- */

.cart-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: white;
  z-index: 10;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart-box {
  width: 95%;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  border-top: 1px solid #e0e0e0;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px 15px 0 0;
}

