.cart-info {
  display: flex;
  align-items: center;
}

.cart-image {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  margin-right: 10px;
}

.cart-details {
  display: flex;
  flex-direction: column;
}

.cart-details h4 {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
  color: #333;
}

.view-menu {
  margin: 0;
  font-size: 14px;
  color: #009944;
  cursor: pointer;
}

/* Actions */
.cart-actions {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.view-cart-btn {
  background: #009944;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

.view-cart-btn:hover {
  background: #009944;
}

.item-count {
  margin: 5px 0 0;
  font-size: 12px;
  color: #666;
}

/* Popup */
.popup {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fafafa;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.2);
  animation: slideUp 0.3s ease-in-out;
  z-index: 20;
  border-radius: 15px 15px 0 0;
}

.popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  background: #ff6347;
  background: #fff;
  color: white;
  border-radius: 10px 10px 0 0;
}

.popup-content {
  padding: 5px;
}

.close-btn {
  background: none;
  border: none;
  color: #004422;
  font-size: 22px;
  cursor: pointer;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.place-order-btn {
  width: 95%;
  padding: 8px;
  margin-top: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}
